body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  background-color: #000000;
}

.App {
  padding: 0;
}

.App-header {
  margin-top: 0;
  padding: 0;
  width: 100%;
}

.navbar-container{
  margin-top: 0;
  padding: 0;
}
.navbar{
  width: 120px;
  height: 100vh;
  color: #ffffff;
  position: fixed;
  margin-top: 0px;
  text-align: center;
}
ul{
  margin-top: 0;
  padding: 0;
  height: 100vh;
  list-style-type: none;
  overflow: auto;
}

li{
  list-style: none;
  display: block;
  width: 120px;
}

.nav-tab{
  box-sizing: border-box;
  padding-top: 3vh;
  background: -webkit-linear-gradient(0deg, #060d13,#12273b);
  height: 20vh;
  border-top: 1px solid #d3d3d3;
}
.material-icons{
  font-size: 40px;
  color: #ffffff;
  display: block;
  text-shadow: 0px 0px 5px rgba(0,0,0,0.42);
  z-index: 99;
}
.nav-text{
  font-family: 'Major Mono Display', monospace;
  font-size: 12px;
  color: #ffffff;
  z-index: 99;
  text-shadow: 0px 0px 5px rgba(0,0,0,0.42);
}
.nav-text-hidden{
  visibility: hidden;
}
.hvr-sweep-to-right:before {
  background: rgba(0, 0, 0, 0.2);
}
.click-scroll{
  cursor: pointer;
}

.page-header{
  display: inline-block;
  justify-content: center;
  text-align: center;
  height: 70px;
  width: 100%;
  background-color: #12273b;
  border-bottom: 2px solid #d3d3d3;
  margin-top: 0;
}

.header-text{
  font-family: 'Major Mono Display', monospace;
  padding-left: 120px;
  color: #d3d3d3;
}

.scrollTop{
  position: fixed;
  height: 50px;
  width: 50px;
  bottom: 25px;
  right: 25px;
  cursor: pointer;
  background-color: #11273b;
  border: 1px solid #ffffff;
  border-radius: 5%;
  opacity: 0.8;
  transition: 0.4s;
  box-shadow: 3px 3px 8px #000000;
  display: none;
}

#small-icon{
  font-size: 24px;
}

.scrollDown{
  position: absolute;
  top: 90%;
  left: 53.5%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 10px;
  cursor: pointer;
  background-color: #11273b;
  border: 1px solid #ffffff;
  border-radius: 5%;
  opacity: 0.8;
  box-shadow: 3px 3px 8px #000000;
}

.scroll-text{
  margin: 0px;
  color: #ffffff;
}

@media screen and (max-width: 1650px){
  .scrollDown{
    left: 54%;
  }
}

@media screen and (max-width: 1350px){
  .scrollDown{
    left: 54.5%;
  }
}

@media screen and (max-width: 1250px){
  .scrollDown{
    left: 55%;
  }
}

@media screen and (max-width: 1150px){
  .scrollDown{
    left: 55.5%;
  }
}

@media screen and (max-width: 1050px){
  .scrollDown{
    left: 56%;
  }
}

@media screen and (max-width: 950px){
  .navbar, li{
    display: none;
  }
  .header-text{
    padding-left: 0;
  }
  .scrollTop{
    display: block;
  }
  .scrollDown{
    left: 50%;
  }
}
.home{
    padding-left: 120px;
    background-image: url(/static/media/background.e8c9df33.JPG);
    background-repeat: round;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    text-align: center;
}

.home-overlay{
    height: 100vh;
    background-color:rgba(0, 26, 51, 0.8);
}

.home-container{
    max-width: 600px;
    display: inline-block;
}

html,
body,
.home,
.home-container{
    min-height: 100vh;
}

.title{
    width: 350px;
    height: 200px;
    opacity: 0.5;
    background: linear-gradient(to right bottom, #001a33 50%, #f2f2f2 50%);
    margin: 0 auto;
    margin-top: 35vh;
    border-radius: 5px;
    padding: 5px;
    text-align: center;
    box-shadow: 20px 20px 25px -10px rgba(211,211,211,1);
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}

.title-text{
    font-size: 40px;
    text-shadow: 3px 3px 1px rgba(0, 0, 0, 1);
    color: #f2f2f2;
    font-family: 'Major Mono Display', monospace;
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}

@-webkit-keyframes rise {
    0%{
        -webkit-transform: translateY(500px);
                transform: translateY(500px);
        -webkit-animation-timing-function: ease-out;
                animation-timing-function: ease-out;
    }
    60%{
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
        -webkit-animation-timing-function: ease-in;
                animation-timing-function: ease-in;
    }
    80%{
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
        -webkit-animation-timing-function: ease-out;
                animation-timing-function: ease-out;
    }
    100%{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        -webkit-animation-timing-function: ease-in;
                animation-timing-function: ease-in;
    }
}

@keyframes rise {
    0%{
        -webkit-transform: translateY(500px);
                transform: translateY(500px);
        -webkit-animation-timing-function: ease-out;
                animation-timing-function: ease-out;
    }
    60%{
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
        -webkit-animation-timing-function: ease-in;
                animation-timing-function: ease-in;
    }
    80%{
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
        -webkit-animation-timing-function: ease-out;
                animation-timing-function: ease-out;
    }
    100%{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        -webkit-animation-timing-function: ease-in;
                animation-timing-function: ease-in;
    }
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@media screen and (max-width: 950px){
    .home{
        padding-left: 0;
    }
}

@media screen and (max-width: 600px){
    .home-container{
        width: 100%;
    }
    .title{
        width: 70%;
        margin: auto;
        margin-top: 50%;
        height: auto;
        padding: 20px;
    }
}

@media screen and (max-width: 400px){
    .title-text{
        font-size: 30px;
    }
}
.about{
    padding-top: 25px;
    padding-bottom: 25px;
    margin: auto;
    padding-left: 120px;
    background-color: #001a33;
    background-image: url("https://www.transparenttextures.com/patterns/cream-pixels.png");
    text-align: center;
}

.about-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    max-width: 1000px;
    margin: auto;
    background-color: #001a33;
    background-image: url("https://www.transparenttextures.com/patterns/cream-pixels.png");
    padding: 10px;
}

html,
body,
.about{
    min-height: 86vh;
}

.profile-img{
    max-height: 250px;
    max-width: 250px;
    margin: 25px;
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    flex: 2 1;
}

.text-container{
    flex: 2 1;
    margin-left: 25px;
    margin-right: 25px;
    min-width: 500px;
    text-align: left;
}

h1, p {
    font-family: 'Rokkitt', serif;
    color: #000000;
    text-shadow: 0px 0px 5px rgba(0,0,0,0.42);
}
p{
    font-size: 22px;
}

@media screen and (max-width: 950px){
    .about{
        padding-left: 0;
    }
    .profile-img{
        flex: 1 1;
    }
    .text-container{
        flex: 1 1;
    }
    .profile-img{
        display: block;
    }
}

@media screen and (max-width: 550px){
    .text-container{
        min-width: 300px;
    }
    p{
        font-size: 20px;
    }
}
.skills{
    display: flex;
    justify-content: center;
    padding-left: 120px;
    padding-bottom: 25px;
    text-align: center;
    background-color: #f2f2f2;
    background-image: url("https://www.transparenttextures.com/patterns/cream-pixels.png");
}

.skills-container{
    max-width: 800px;
    margin: auto;
    background-color: #f2f2f2;
    background-image: url("https://www.transparenttextures.com/patterns/cream-pixels.png");
    padding: 25px;
}

html,
body,
.skills,
.skills-container{
    min-height: 86vh;
}

.logo-container{
    display: inline-block;
}

.category{
    display: block;
    text-align: center;
}

.logos{
    height: 120px;
    width: auto;
    box-sizing: border-box;
    padding: 10px;
}

.logos:hover{
    -webkit-animation-name: enlarge;
            animation-name: enlarge;
    -webkit-animation-duration: .2s;
            animation-duration: .2s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}

@-webkit-keyframes enlarge {
    from {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    to {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
}

@keyframes enlarge {
    from {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    to {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
}

@media screen and (max-width: 950px){
    .skills, .header-text{
        padding-left: 0;
    }
    .logos{
        max-height: 90px;
    }
}
